<template>
  <br>
  <InputText v-model="description" placeholder="Digite aqui" v-focus></InputText>
  <Button id="btn-save" @click="saveTodo">Salvar</Button>
</template>

<script>
export default {
  data() {
    return {
      description: ""
    }
  },
  props: {
    todo: Object
  },
  created() {
    if (this.todo) {
      this.description = this.todo.description
    }
  },
  methods: {
    saveTodo() {
      this.$emit("saveTodo", { ...this.todo, description: this.description })
    }
  },
  directives: {
    focus: {
      mounted(el) {
        el.focus()
      }
    }
  }
}
</script>

