<template>
  <h1>To-Do App</h1>
  <div class="todo-list" v-if="!editMode">
    <Button id="btn-new" @click="newTodo">Novo item</Button>
    <todo-list :todos="todos" @deleteTodo="deleteTodo" @editTodo="editTodo"></todo-list>
  </div>
  <div class="todo-item" v-if="editMode">
    <Button id="btn-back" class="p-button-danger" @click="back">Voltar</Button>
    <todo-item @saveTodo="saveTodo" :todo="todo"></todo-item>
  </div>
</template>


<script>
import TodoList from './components/TodoList'
import TodoItem from './components/TodoItem'

export default {
  components: {
    TodoList,
    TodoItem
  },
  data() {
    return {
      editMode: false,
      todos: [],
      todo: null,
      nextId: 1
    }
  },
  methods: {
    newTodo() {
      this.todo = null
      this.editMode = true
    },
    back() {
      this.editMode = false
    },
    saveTodo(todo) {
      if (todo.id){
        const index = this.todos.findIndex((item) => item.id === todo.id)
        this.todos[index] = todo
      } else {
        todo = {id: this.nextId, ...todo}
        this.todos.push(todo)

        this.nextId++
        localStorage.setItem("nextId", this.nextId)
      }
      localStorage.setItem("todos", JSON.stringify(this.todos))
      this.editMode = false
    },
    deleteTodo(index) {
      this.todos.splice(index, 1)
      localStorage.setItem("todos", JSON.stringify(this.todos))
    },
    editTodo(index) {
      this.todo = this.todos[index]
      this.editMode = true
    }
  },
  created() {
    const todos = localStorage.getItem("todos")
    if (todos) {
      this.todos = JSON.parse(todos)
    }
    const nextId = localStorage.getItem("nextId")
    if (nextId) {
      this.nextId = parseInt(nextId)
    }
  }
}
</script>


<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  color: #2c3e50;
  margin: 0 auto;
  margin-top: 20px;
  width: 100%;
  text-align: center;
}
#btn-new {
  margin: 0 0 2em 0;
}
#btn-back {
  margin: 0 0 2em 0;
}
#btn-save {
  margin: 0 0 0 .5em;
}
</style>
